"use client";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import { PROCEDURES } from "../data/procedures";
import { CardContainer, CardBody, CardItem } from "./ui/3d-card";

// Nuevas imágenes
import Imagenrino from "../img/Rinoplastia_CARD.png";
import ImagenMamas from "../img/AumentodeMamasCard.jpg";
import ImagenLipo from "../img/Liposuccion_Card.png";

/** Elige imagen según slug/title si no viene p.image */
function pickImage(p) {
  if (p?.image) return p.image; // respeta la imagen definida en data
  const slug = (p?.slug || "").toLowerCase();
  const title = (p?.title || "").toLowerCase();

  if (slug.includes("rino") || title.includes("rino")) return Imagenrino;
  if (slug.includes("mama") || title.includes("mama")) return ImagenMamas;
  if (slug.includes("lipo") || title.includes("lipo")) return ImagenLipo;

  // Fallback genérico
  return Imagenrino;
}

export default function Procedures() {
  // Trackeo de carga de imágenes para el skeleton
  const [loaded, setLoaded] = useState({}); // { [slug]: true }

  return (
    <section
      className="section procs marked"
      aria-labelledby="procedimientos-heading"
    >
      <div className="container">
        <header className="procs__head">
          <h2
            id="procedimientos-heading"
            className="h2"
            style={{
              fontSize: "clamp(2rem,4vw,2.75rem)",
              marginBottom: ".25rem",
            }}
          >
            Nuestros Procedimientos
          </h2>
          <p className="procs__sub">
            Descubra cómo podemos ayudarle a alcanzar sus objetivos estéticos. Ofrecemos una amplia
            variedad de procedimientos faciales y corporales, utilizando las técnicas más avanzadas y seguras.
          </p>
        </header>

        <div className="procs__grid" role="list">
          {PROCEDURES.map((p, idx) => {
            const imgSrc = pickImage(p);
            const slugKey = p.slug || `idx-${idx}`;

            return (
              <article
                key={slugKey}
                className="card procs-card"
                role="listitem"
                aria-labelledby={`proc-${slugKey}-title`}
                style={{ "--stagger": `${idx * 90}ms` }}
              >
                <CardContainer
                  className="card3d"
                  style={{
                    "--card-tilt-max": "6deg",
                    "--card-tilt-duration": "480ms",
                  }}
                  aria-hidden="false"
                >
                  <CardBody
                    className="is-dark"
                    style={{
                      padding: "1rem",
                      minHeight: "360px",
                      display: "flex",
                      flexDirection: "column",
                      borderRadius: "14px",
                    }}
                  >
                    {/* Capa inclinable (contenido visual) */}
                    <div className="td3d-tilt" style={{ flex: "1 1 auto" }}>
                      <CardItem
                        translateZ="26"
                        className="card__media procs-media"
                        style={{
                          borderRadius: "var(--radius-lg, 12px)",
                          minHeight: "180px",
                          display: "grid",
                          placeItems: "center",
                          overflow: "hidden",
                          position: "relative",
                        }}
                      >
                        {/* Skeleton shimmer mientras carga */}
                        <div
                          className={`procs-skeleton ${
                            loaded[slugKey] ? "is-loaded" : ""
                          }`}
                          aria-hidden
                        />

                        <img
                          src={imgSrc}
                          alt={p.title}
                          className="procs-img"
                          loading="lazy"
                          onLoad={() =>
                            setLoaded((s) => ({ ...s, [slugKey]: true }))
                          }
                          onError={(e) => {
                            if (!e.currentTarget.dataset.fallbacked) {
                              e.currentTarget.dataset.fallbacked = "1";
                              e.currentTarget.src = Imagenrino; // fallback
                              setLoaded((s) => ({ ...s, [slugKey]: true }));
                            }
                          }}
                          style={{
                            width: "100%",
                            height: "100%",
                            objectFit: "cover",
                            display: "block",
                            transform: "scale(1)",
                            transition: "transform .55s ease, opacity .35s ease",
                            opacity: loaded[slugKey] ? 1 : 0,
                          }}
                        />
                      </CardItem>

                      <CardItem
                        translateZ="18"
                        as="div"
                        className="card__body procs-body"
                        style={{
                          padding: "1rem 0 0 0",
                          display: "flex",
                          flexDirection: "column",
                          gap: ".5rem",
                        }}
                      >
                        <h3
                          id={`proc-${slugKey}-title`}
                          className="card__title"
                          style={{ margin: 0 }}
                        >
                          {p.title}
                        </h3>
                        <p className="card__text" style={{ margin: 0 }}>
                          {p.short}
                        </p>
                      </CardItem>
                    </div>

                    {/* Footer / CTA */}
                    <div
                      className="td3d-footer"
                      style={{
                        marginTop: "auto",
                        display: "flex",
                        justifyContent: "flex-end",
                        gap: ".5rem",
                      }}
                    >
                      <Link
                        to={`/procedimiento/${p.slug}`}
                        className="btn btn--ghost procs-cta"
                        aria-label={`Conocer más sobre ${p.title}`}
                      >
                        Conocer más
                      </Link>
                    </div>
                  </CardBody>
                </CardContainer>
              </article>
            );
          })}
        </div>
      </div>
    </section>
  );
}
